  .text-container hr {
    border: none;
    width: 10%;
    height: 3px;
    border-radius: 10px;
    background: #ff3366;
    margin-bottom: 10px;
  }
  .text-container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .icon {
    width: 80px;
    height: auto;
    margin-bottom: 20px;
  }
  
  h3 {
    font-size: 24px;
    color: #333;
    margin-bottom: 10px;
  }
  
  p {
    font-size: 16px;
    color: #666;
    margin-bottom: 20px;
    line-height: 1.5;
  }
  

  
  .read-more{
    background-color: #ff3366;
    color: #fff;
    border: none;
    padding: 10px 20px;
    font-size: 16px;
    font-weight: 600;
    border-radius: 5px;
    cursor: pointer;
    background-color: #ff3366;
    border-radius: 5px;
    box-shadow: 5px 5px 7px rgba(0, 0, 0, 0.7); 
  
    transition: all transform 0.3s;
    transition-timing-function:ease-out ;
  }
  
  .read-more:hover{
    transform: translateY(-10px);
  
  }

  
  /* Media query for MacBook Air (typically 1440px width) */
@media only screen and (max-width: 1440px) {
    .grid-layout {
      grid-template-columns: fr;
    }
  
    .grid-item {
      padding: 15px;
    }
  
    h3 {
      font-size: 22px;
    }
  
    p {
      font-size: 14px;
    }
  
    .read-more {
      font-size: 14px;
      padding: 8px 16px;
    }
  }
  
  /* Media query for tablets (typically between 600px and 768px width) */
  @media only screen and (max-width: 768px) and (min-width: 600px) {
    .grid-layout {
      grid-template-columns: 1fr;
    }
  
    .grid-item {
      padding: 15px;
    }
  
    h3 {
      font-size: 22px;
    }
  
    p {
      font-size: 14px;
    }
  
    .read-more {
      font-size: 14px;
      padding: 8px 16px;
    }
  }
  
  /* Media query for mobile screens (typically less than 600px width) */
  @media only screen and (max-width: 600px) {
    .grid-layout {
      grid-template-columns: 1fr;
    }
  
    .grid-item {
      padding: 10px;
    }
  
    h3 {
      font-size: 20px;
    }
  
    p {
      font-size: 12px;
    }
  
    .read-more {
      font-size: 12px;
      padding: 6px 12px;
    }
  }
  