.heading{
  text-align: left;
  padding: 40px;
  color: #ff3366;
  font-size: 35px;
  font-weight: 700;
}

.blog img{
  height: 200px;
  width: 200px;
  border-radius: 7px;
}

.cards{

  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
  flex-wrap: wrap;

}

.cards img{
  width: 100%;
  height: auto;
}

.card1{
  height: 570px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  transition: transform 0.3s ease-in-out;

}
.card1:hover{
  transform: scale(1.02);
}

.card1 h3{
  text-align: left;
  color: white;
  font-family: "Roboto", sans-serif;
  font-weight: 300;
  font-style: normal;
  line-height: 30px;
  font-size: 29px;
  text-wrap: nowrap;
}

.card1 p{
  text-align: left;
  color: white;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 14px;
  padding-top: 22px;
  line-height: 21px;
  padding-bottom: 10px;
}

.details{
  /* margin-top: 10px; */
  display: flex;
  align-self: flex-end;
  gap:30px;

}
.details img{
  width: 40px;
  height: 40px;
  border-radius: 50%;
  padding-top: 22px;

}